import React, { Component, Fragment } from 'react'

import { Redirect } from 'react-router-dom'

import Dialog from '/ui/Dialog'
import DialogTitle from '/ui/DialogTitle'
import DialogContent from '/ui/DialogContent'
import DialogActions from '/ui/DialogActions'
import Heading from '/ui/Heading'
import Button from '/ui/Button'

class Refund extends Component {
  state = {
    isOpen: true,
  }
  handleDialogClose = _ => {
    this.setState({
      isOpen: false,
    })
  }

  render() {
    const { isOpen } = this.state
    return (
      <Fragment>
        <If condition={!isOpen}><Redirect to='/'/></If>
        <Dialog open={isOpen} onClose={this.handleDialogClose} maxWidth='md' scroll='paper' fullWidth>
          <DialogTitle>
            <Heading type='h2' className='m-t-0 m-b-0'>Правила возврата средств</Heading>
          </DialogTitle>
          <DialogContent>
            <Heading type='h3'>1.&nbsp;Заявление на возврат денежных средств</Heading>

            <p>1.1.&nbsp;Обращаем Ваше внимание: что приобретая Билеты в ООО "АРена Лабс Р", Вы заключаете Договор оказания услуг по бронированию, оформлению и реализации Билетов (Электронных билетов) в Сети Парков виртуальных развлечений (здесь и далее по тексту – «Публичная оферта»), в соответствии с которым ООО "АРена Лабс Р" предоставляет Вам услугу приобретения Билетов на условиях, оговоренных в Публичной оферте.&nbsp;</p>

            <p>1.2.&nbsp;Согласие с этими условиями, Вы подтверждаете фактом оплаты итоговой стоимости Билетов. Документальным подтверждением Вашего согласия является чек, либо квитанция об оплате итоговой стоимости Билетов.</p>

            <Heading type='h3'>2.&nbsp;Порядок возврата билетов</Heading>

            <p>2.1.&nbsp;В случае отмены/замены/переноса к возврату принимаются только Билеты, купленные в компании АРена Лабс Р. Возврат Билетов производится в соответствии с условиями (срок, время, место возврата Билетов), установленными АРена Лабс Р.</p>

            <p>2.2.&nbsp;Для возврата Билетов, приобретенных в кассах АРена Лабс Р в Сети Парков виртуальных развлечений, за наличный расчет, при себе необходимо иметь:</p>

            <ul>
              <li>оригинал Билета, подлежащего возврату</li>
              <li>паспорт Гражданина РФ</li>
            </ul>

            <p>2.3.&nbsp;Для возврата билетов, приобретенных на кассах АРена Лабс Р и оплаченных банковской картой, необходимо иметь:</p>

            <ul>
              <li>оригинал Билета, подлежащего возврату</li>
              <li>паспорт Гражданина РФ</li>
              <li>чек от банковского терминала с кодом авторизации, который выдается при покупке Билета банковской картой;</li>
              <li>в случае утери чека - банковские реквизиты карты, с которой проводилась оплата: номер карты, номер расчетного счета этой карты, наименование банка, БИК банка. Номер карты и номер расчетного счета должны отличаться.</li>
            </ul>

            <p>2.4.&nbsp;Для возврата Билетов, напечатанных на бумажном бланке АРена Лабс Р и оплаченных на сайте https://tickets.arenaspace.ru/, необходимо иметь:&nbsp;</p>

            <ul>
              <li>оригинал Билета, подлежащего возврату</li>
              <li>паспорт Гражданина РФ</li>
              <li>чек от банковского терминала с кодом авторизации, который выдается при покупке Билета банковской картой;</li>
              <li>в случае утери чека - банковские реквизиты карты, с которой проводилась оплата: номер карты, номер расчетного счета этой карты, наименование банка, БИК банка. Номер карты и номер расчетного счета должны отличаться</li>
            </ul>

            <p>2.5.&nbsp;Для возврата электронных Билетов необходимо:&nbsp;</p>

            <ul>
              <li>заявление на возврат Билетов;&nbsp;</li>
              <li>скан паспорта гражданина РФ;&nbsp;</li>
              <li>электронные Билеты, подлежащие возврату</li>
            </ul>

            <p>2.6.&nbsp;Документы для осуществления возврата Билетов можно подать лично в офисе либо прислать на e-mail: booking@arenaspace.ru</p>

            <p>2.7.&nbsp;Рассмотрение данных заявлений производится только в рабочие дни</p>

            <p>2.8.&nbsp;Возврат по желанию можно осуществить не позднее 12 часов до начала мероприятия, исключительно, по согласованию с организатором мероприятия.&nbsp;</p>

            <p>2.9.&nbsp;Заявления на e-mail не принимаются менее чем за 12 часов до начала мероприятия.&nbsp;&nbsp;</p>

            <p>В этом случае необходимо обратиться в центральную кассу Арена Спейс ЦДМ по адресу: Театральный пр‑д, д.5/1, при себе иметь:</p>

            <ul>
              <li>электронные Билеты, подлежащие возврату;</li>
              <li>паспорт гражданина РФ;</li>
              <li>банковские реквизиты карты, с которой проводилась оплата: номер карты, номер расчетного счета этой карты, наименование банка, БИК банка. Номер карты и номер расчетного счета должны отличаться</li>
            </ul>

            {/* <p><a href='/docs/blank_refund.doc' className='btn btn--default' title='Скачать бланк заявления doc'>Скачать бланк заявления</a></p> */}

            <p>2.10.&nbsp;Сумма сервисного сбора, стоимость доставки и прочих дополнительных услуг, оказанных ООО «АРена Лабс Р», возврату не подлежат, согласно&nbsp; Публичной оферте. С Публичной офертой можно ознакомиться в сети Интернет по адресу: https://arenaspace.ru/dokumentyi/publichnaya-oferta/ или на кассах в Сети Парков виртуальных развлечений АРена Лабс Р.&nbsp;</p>

            <p>2.11.&nbsp;Срок рассмотрения заявлений и возврата составляет 10 рабочих дней.&nbsp;</p>

            <p>2.12.&nbsp;Если Вы оплачивали Билеты банковской картой, Вы можете получить деньги только по безналичному расчету, для этого Вам необходимо сообщить нам подробные реквизиты. Во всех остальных случаях деньги возвращаются наличными;</p>

            <p>ВНИМАНИЕ! БЛАНКИ БИЛЕТОВ С ЛЮБЫМИ ИСПРАВЛЕНИЯМИ И ЗАКЛЕЕННОЙ ИНФОРМАЦИЕЙ НЕ ДЕЙСТВИТЕЛЬНЫ И ВОЗВРАТУ НЕ ПОДЛЕЖАТ!</p>
          </DialogContent>
          <DialogActions>
            <Button variant='contained' size='large' color='primary' onClick={this.handleDialogClose}>Закрыть</Button>
          </DialogActions>
        </Dialog>
      </Fragment>
    )
  }
}

export default Refund
